<template>
  <div>
    <el-card>
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="预定列表" name="ward_list">
        <el-row>
          <el-col :span="24">
            <div class="grid-content bg-purple-dark">
              <button>场地管理</button>
              <button>资源管理</button>
              <button>组合查询</button>
              <button>刷新</button>
            </div>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="预定状态" name="ward_status">

      </el-tab-pane>
      <el-tab-pane label="预定时间段" name="ward_times">

      </el-tab-pane>
    </el-tabs>
    </el-card>
  </div>
</template>
<script>
    export default {
        name: "meetsteward",
        data(){
          return {
            activeName: 'ward_list', //状态切换
            loading : true, // loading状态
          }
        },
      beforeCreate:{

      },
      created:function () {

      },
      methods:{
        handleClick(tab, event) {
          console.log(tab, event);
        }
      }
    }
</script>

<style scoped lang="less">
  body{
    font-size: 0.8vw;
  }
.el-card{
  width: 94vw;
  height: 90vh;
  .el-tabs{
    margin-left: -0.8vw;
  }
  .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #FFFFFF;
    button{
      height: 4vh;
      width: 5vw;
      color: #ffffff;
      font-size: 0.8vw;
      line-height: 4vh;
      border: none;
      border-radius: 0.3vw;
      background: #4488E9;
    }
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
}
</style>



